import React, { useCallback, useState, useRef, useEffect } from "react"
import {
  Icon,
  Label,
  HintContainer,
  ErrorLabel,
  HintLabel,
} from "../TextInput/Atom.tsx"

import {
  Delete,
  Input,
  Preview,
  PreviewImg,
  PreviewImgWrapper,
  PreviewImgContainer,
  FileUploadContainer,
  DragDropText,
} from "./Atom.tsx"
import Upload from "../../assets/upload.png"
import ProgressBar from "./ProgressBar.tsx"
import { Row, Column as Col } from "../StyledComponent/index.tsx"

export interface PicUploadProps {
  onChange?: any
  value?: any
  style?: any
  name?: string
  customRef?: any
  isFormInputElement?: boolean
  required?: boolean
  hint?: string
  imageSrc?: any
  label?: string
  acceptedFormat: string
  deleteFile?: any
  progress?: any
  productVariantIndex?: any
}

export interface RefProps {
  getValue: () => string
  validate: () => any
  name: string
}

const PicUploadMultiple = (props: PicUploadProps) => {
  const {
    onChange,
    value,
    customRef,
    name,
    required,
    hint,
    imageSrc,
    label,
    acceptedFormat,
    deleteFile,
    progress,
    productVariantIndex
  } = props
  const getInitValue = useCallback(() => {
    return value || []
  }, [value])

  const [selectedFiles, setSelectedFiles] = useState(getInitValue());
  const [imagePreviews, setImagePreviews] = useState(getInitValue());
  let uploadRef = useRef<any>()
  const [error, setError] = useState({ error: false, message: "" })

  const validate = useCallback(
    (input: any) => {
      let newState = {
        error: false,
        message: "",
      }
      if (!input && required) {
        newState = {
          error: true,
          message: label + " is missing",
        }
      }
      setError(newState)
      return newState
    },
    [label, required]
  )

  const onChangeValue = (e: any) => {
    let newState = {
      error: false,
      message: "",
    }
    let images = []
    let uploadedFileTypes: any = []
    if(e.target.files && e.target.files.length > 0){
      for (let i = 0; i < e.target.files.length; i++) {
        // @ts-ignore-next-line
        images.push(URL.createObjectURL(e.target.files[i]));
        uploadedFileTypes.push(e.target.files[i].type)
      } 
    }
    let allFilesAccepted = true 
    uploadedFileTypes.every((fileType: any)=>{
      if(!acceptedFormat.includes(fileType)){
        allFilesAccepted = false 
        return false
      }
      else{
        return true
      }
    })
    if(allFilesAccepted){
      let newFiles = [...selectedFiles, ...e.target.files]
      let newPreviews = [...imagePreviews, ...images]
      setSelectedFiles(newFiles)
      setImagePreviews(newPreviews)
      if(typeof productVariantIndex != 'undefined')
      onChange && onChange(e.target.files,productVariantIndex)
      else 
      onChange && onChange(e.target.files)
    } else {
      newState = {
        error: true,
        message: `File type should be ${acceptedFormat} format only`,
      }
    }
    setError(newState)
  }

  const removeValue = async (i: any) => {
    if(deleteFile){
      let result
      if(typeof productVariantIndex != 'undefined')
      result = await deleteFile(i, productVariantIndex)
      else 
      result = await deleteFile(i)
      if(result == "success"){
        let selectedFilesCopy = [...selectedFiles]
        let imagePreviewsCopy = [...imagePreviews]
        selectedFilesCopy.splice(i, 1)
        imagePreviewsCopy.splice(i, 1)
        setSelectedFiles(selectedFilesCopy)
        setImagePreviews(imagePreviewsCopy)
      }
    }
  }

  useEffect(() => {
    if(customRef)
    customRef({ value: selectedFiles, validate, name: name || "" })
  }, [customRef, selectedFiles, name, validate])
  useEffect(() => {
    setSelectedFiles(value || [])
    setImagePreviews(value || [])
  }, [value])
  return (
    <>
      {imageSrc ? <Icon src={imageSrc} alt="" /> : null}
      {label ? (
        <Label>
          {required && <span className="required">* </span>} {label}:{" "}
        </Label>
      ) : null}

      <Row>
        {(
          <>
            <Col lg={4}>
              <FileUploadContainer onClick={() => uploadRef.current?.click()}>
                <Input src={Upload} alt="" />
                <DragDropText>Click here to upload</DragDropText>
              </FileUploadContainer>{" "}
            </Col>
            <input
              type="file"
              multiple
              accept={acceptedFormat}
              style={{ display: "none" }}
              ref={uploadRef}
              onChange={onChangeValue}
            />{" "}
          </>
        )}

      </Row>
      {imagePreviews.map((preview: any, index: any) => {
        return (
          <div>
            <Preview>Preview</Preview>
            <PreviewImgContainer>
              <PreviewImgWrapper>
                <PreviewImg src={preview} alt="" />
              </PreviewImgWrapper>
              <Delete onClick={() => removeValue(index)}>Delete</Delete>
              {(progress?.[index] && progress?.[index] > 0 && progress?.[index] < 100) ? (<ProgressBar
                bgcolor="#306297"
                progress={progress?.[index]}
                height={5}
                size={selectedFiles?.[index]?.size}
                name={selectedFiles?.[index]?.name}
              />) : progress?.[index] && progress?.[index] == -1 ? (
                 <span style={{color: "red"}}>
                   Could not upload Image. Something went wrong.
                 </span>
              ) : null}
            </PreviewImgContainer>
          </div>
        )
      })}
      <HintContainer>
        {error.error ? <ErrorLabel>{error.message}</ErrorLabel> : <div />}
        {hint ? <HintLabel>{hint}</HintLabel> : <div />}
      </HintContainer>
    </>
  )
}

export default PicUploadMultiple
