import React,{ useCallback} from 'react';
import { useDispatch, useSelector } from "react-redux";
import { cartActions } from "../../store/cart";
import { isEqual } from 'lodash';
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import "./index.css";
import { useNavigate } from 'react-router-dom';
import trackUserEvent from '../../helpers/trackUserEvent';
const MySwal = withReactContent(Swal)

const AddCartBuyNow = (props) => {
    const {
        product,
        selectedProduct
    } = props

    const selectedList = useSelector(state=>state.cart.itemsList)
    const isUserLogin = useSelector(state => state.user.currentUser.userId ? true : false)
    const dispatch = useDispatch();
    const navigate = useNavigate();

    function checkIfProductExists(product){
        return (product.productId == this.id && isEqual(product.attributeCombination , selectedProduct.attributeCombination))
    }

    const handleAddToCart = useCallback(() => { 
        if(!selectedProduct?.isActive) return      
        let newList = JSON.parse(JSON.stringify(selectedList))
        let productFound = newList.findIndex(checkIfProductExists, product)
        if(productFound == -1){
            newList.push({
                productId: product.id,
                count: 1,
                productVariantId: selectedProduct.id,
                attributeCombination: selectedProduct.attributeCombination
            })
        }
        else{
            newList[productFound].count += 1
        }
        dispatch(cartActions.addToCart(newList))
        MySwal.fire({
          title: <strong>Product Added!</strong>,
          html: <p>Product Added to Cart Successfully</p>,
          icon: 'success'
        })
        trackUserEvent("Add To Cart")
    },[selectedList,dispatch, product, selectedProduct])

    
    const handleBuyNow = useCallback(() => { 
        if(!selectedProduct?.isActive) return      
        let newList = JSON.parse(JSON.stringify(selectedList))
        let productFound = newList.findIndex(checkIfProductExists, product)
        if(productFound == -1){
            newList.push({
                productId: product.id,
                count: 1,
                productVariantId: selectedProduct.id,
                attributeCombination: selectedProduct.attributeCombination
            })
        }
        else{
            newList[productFound].count += 1
        }
        dispatch(cartActions.addToCart(newList))
        if (isUserLogin) {
            navigate("/checkout",{state: {showPopup: true}})
        }
        else {
            MySwal.fire({
                text: "You need to Login first to proceed",
                buttons: {
                    login: {
                        text: "Login",
                        value: "login",
                    }
                },
            })
                .then((value) => {
                    if (value?.isConfirmed) {
                        navigate('/sign-in?target=checkout')
                    }
                });
        }
    },[selectedList,dispatch, product, selectedProduct])

    return (
        <>
        <div className="addCartBuyNow d-block d-lg-none">
            <div className="container-fluid" id="addCartBuyNow">
            {selectedProduct?.isActive ? (            
                    <div className="row">
                        <div className="col-6">
                            <button className="btn btn-dark w-100" onClick={handleAddToCart}>Add to Cart</button>
                        </div>
                        <div className="col-6">
                            <button className="btn btn-success w-100" onClick={handleBuyNow}>Buy Now</button>
                        </div>
                    </div>) : (
                    <div className="row">
                        <div className="col-12">
                            <button className="btn btn-dark w-100">Out of Stock</button>
                        </div>
                    </div>
                )
            }
            </div>
        </div>
        </>
    )
}

export default AddCartBuyNow;